    </v-card-title>

    <v-window v-model="step">
      <!-- Lista de Recibos -->
      <v-window-item :value="1">
        <!-- Filtros -->
        <v-container>
          <v-card outlined elevation="2" tile class="ma-2 pa-2">
            <v-row>
              <!-- Empresa Filtro -->
              <v-col cols="12" md="4" lg="4">
                <v-select
                  v-model="empresaFiltroSelected"
                  :items="empresas"
                  item-text="nombre"
                  item-value="id"
                  label="Empresa"
                  @change="getRecibosFiltrados"
                  return-object
                  single-line
                  no-data-text="Sin datos"
                  clearable
                ></v-select>
              </v-col>

              <!-- Empleado Filtro-->
              <v-col cols="12" md="4" lg="4">
                <v-select
                  v-model="empleadoFiltroSelected"
                  :items="empleados"
                  :item-text="(item) => `${item.apellido}, ${item.nombre}`"
                  item-value="id"
                  label="Empleado"
                  @change="getRecibosFiltrados"
                  return-object
                  single-line
                  no-data-text="Sin datos"
                  clearable
                ></v-select>
              </v-col>

              <!-- Período Filtro-->
              <v-col cols="12" md="3" lg="3">
                <v-dialog
                  ref="dialogPeriodoFiltro"
                  v-model="periodoFiltroModal"
                  :return-value.sync="periodoFiltroFecha"
                  persistent
                  width="290px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="periodoFiltroFecha"
                      label="Período"
                      @click:clear="limparFiltroPeriodo"
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                      clearable
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="periodoFiltroFecha"
                    type="month"
                    scrollable
                    locale="es"
                  >
                    <v-spacer></v-spacer>
                    <v-btn
                      text
                      color="primary"
                      @click="periodoFiltroModal = false"
                    >
                      Cancelar
                    </v-btn>
                    <v-btn text color="primary" @click="aceptarFiltroPeriodo()">
                      Aceptar
                    </v-btn>
                  </v-date-picker>
                </v-dialog>
              </v-col>
            </v-row>
          </v-card>

          <!-- Duplicar Recibo -->
          <v-dialog v-model="dialogDuplicate" persistent max-width="290">
            <v-card>
              <v-toolbar color="primary" dark dense elevation="0">
                Recibo {{ editedItem.recibo }}
