              <v-card-title class="text-h6"> </v-card-title>
              <v-card-text class="text-body-2">
                ¿Duplica el Recibo {{ editedItem.recibo }}?
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="grey darken-1"
                  text
