                                  ></v-text-field>
                                </v-col>
                                <!-- Alícuota -->
                                <v-col cols="12" md="2">
                                  <v-text-field
                                    v-model="editedItemRecibo.alicuota"
                                    label="Alícuota"
                                    :rules="[
                                      reglas.soloNumerosPositivos,
                                      reglas.maximoNumeros(4),
                                      reglas.numerosHasta(100),
                                    ]"
                                    @change="totalConcepto"
                                  ></v-text-field>
                                </v-col>
                                <!-- Total -->
                                <v-col cols="12" md="4">
                                  <v-text-field
                                    v-model="editedItemRecibo.total"
                                    label="Total"
                                    disabled
                                  ></v-text-field>
                                </v-col>
                              </v-row>
                            </v-form>
                          </v-container>
                        </v-card-text>

                        <!-- actions Detalle Recibo -->
                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn
                            color="blue darken-1"
                            text
