                    class="mr-2"
                    @click="editItemDetalleRecibo(item)"
                  >
                    mdi-pencil
                  </v-icon>
                  <v-icon small @click="deleteItemDetalleRecibo(item)">
                    mdi-delete
                  </v-icon>
                </template>

                <template slot="body.append">
                  <tr style="background-color: lightgray">
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="pink--text subtitle">Subtotal</th>
                    <th>{{ totalRemuneracionesComputed }}</th>
                    <th>{{ totalExencionesComputed }}</th>
                    <th>{{ totalDescuentosComputed }}</th>
                    <th></th>
                    <th></th>
