                <template slot="body.append">
                  <tr style="background-color: lightgray">
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="green--text title">Total</th>
                    <th class="title">{{ totalNetoComputed }}</th>
                    <th></th>
                  </tr>
                </template>
              </v-data-table>
            </v-col>
          </v-row>
        </v-container>
      </v-window-item>

