<script>
import { reglasMixin } from "../mixins/reglasMixin";
import { numerosPalabrasMixin } from "../mixins/numerosPalabrasMixin";
import Localbase from "localbase";

let db = new Localbase("db");
db.config.debug = false;

export default {
  name: "RecibosView",
  mixins: [reglasMixin, numerosPalabrasMixin],
  data: (vm) => ({
    fecha: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substring(0, 10),
    dateFormatted: vm.formatDate(
      new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substring(0, 10)
    ),
    menuFecha: false,
    // Período
    periodoFecha: new Date().toISOString().substring(0, 7),
    periodoModal: false,

    periodoFiltroFecha: null,
    periodoFiltroModal: false,

    step: 1,
    dialog: false,
    dialogDelete: false,
    dialogDuplicate: false,
    headers: [
      { text: "Recibo N°", value: "recibo", sortable: true },
      { text: "Período", value: "periodo", sortable: true },
      {
        text: "Fecha",
        align: "start",
        sortable: true,
        value: "fecha",
      },
      { text: "Empleado", value: "empleado", sortable: true },
      { text: "Empleador", value: "empresa", sortable: true },
      { text: "", value: "actions", sortable: false },
    ],
    recibos: [],
    editedIndex: -1,
    editedItem: {
      id: "",
      recibo: "",
      periodo: "",
      fecha: "",
      formaPago: "",
      idEmpleado: "",
      empleado: "",
      empresa: "",
      idEmpresa: "",
      detalle: [],
    },
    defaultItem: {
      id: "",
      recibo: "",
      periodo: "",
      fecha: "",
      formaPago: "",
      idEmpleado: "",
      empleado: "",
      empresa: "",
      idEmpresa: "",
      detalle: [],
    },

    empresaSelected: null,
    empresaFiltroSelected: null,
    empresas: [],

    empleadoSelected: {},
    empleadoFiltroSelected: null,
    empleados: [],

    //Detalle Recibo
    conceptoSelected: null,
    conceptos: [],
    dialogDetalleRecibo: false,
    dialogDetalleReciboDelete: false,
    headersDetalleRecibo: [
      {
        text: "Código",
        align: "start",
        sortable: false,
        value: "codigo",
      },
      { text: "Concepto", value: "concepto" },
      { text: "Unidades", value: "unidades" },
      { text: "Alícuota", value: "alicuota" },
      { text: "Unitario", value: "unitario" },
      { text: "Retenciones", value: "retenciones" },
      { text: "Exenciones", value: "exenciones" },
      { text: "Descuentos", value: "descuentos" },

      { text: "", value: "actions", sortable: false },
    ],
    itemsRecibo: [],
    editedIndexRecibo: -1,
    editedItemRecibo: {
      id: 0,
      codigo: 0,
      concepto: "",
      conceptoSelected: {},
      unidades: "",
      unitario: "",
      alicuota: "",
      retenciones: "",
      exenciones: "",
      descuentos: "",
      total: "",
    },
    defaultItemRecibo: {
      id: 0,
      codigo: 0,
      concepto: "",
      conceptoSelected: {},
      unidades: "",
      unitario: "",
      alicuota: "",
      retenciones: "",
      exenciones: "",
      descuentos: "",
      total: "",
    },
    totalNeto: 0,
  }),

  computed: {
    formTitleDetalleRecibo() {
      return this.editedIndex === -1 ? "Nuevo Concepto" : "Editar Concepto";
    },
    currentTitle() {
      switch (this.step) {
        case 1:
          return "Recibos";
        case 2:
          return "Nuevo Recibo";
        default:
          return "";
      }
    },
    computedFecha: {
      get() {
        return this.formatDate(this.fecha);
      },
      set(fecha) {
        return fecha;
      },
    },
    ingresoComputed: {
      get() {
        let ingreso = "";
        let empleado = this.editedItem.empleadoSelected;
        if (empleado != undefined) {
          ingreso = new Date(
            empleado.ingreso + " 00:00:00"
          ).toLocaleDateString();
        }
        return ingreso;
      },
      set(ingreso) {
        return ingreso;
      },
    },
    totalNetoTextoComputed: {
      get() {
        return this.NumeroALetras(this.totalNetoComputed);
      },
      set(totalNeto) {
        return totalNeto;
      },
    },
    totalRemuneracionesComputed: {
      get() {
        let sum = this.itemsRecibo.reduce((accumulator, object) => {
          if (object.retenciones != "") {
            return accumulator + parseFloat(object.retenciones);
          } else {
            return accumulator;
          }
        }, 0);
        return parseFloat(sum).toFixed(2);
      },
      set(total) {
        return total;
      },
    },
    totalExencionesComputed: {
      get() {
        let sum = this.itemsRecibo.reduce((accumulator, object) => {
          if (object.exenciones != "") {
            return accumulator + parseFloat(object.exenciones);
          } else {
            return accumulator;
          }
        }, 0);
        return parseFloat(sum).toFixed(2);
      },
      set(total) {
        return total;
      },
    },
    totalDescuentosComputed: {
      get() {
        let sum = this.itemsRecibo.reduce((accumulator, object) => {
          if (object.descuentos != "") {
            return accumulator + parseFloat(object.descuentos);
          } else {
            return accumulator;
          }
        }, 0);
        return parseFloat(sum).toFixed(2);
      },
      set(total) {
        return total;
      },
    },
    totalNetoComputed: {
      get() {
        let totalRemuneraciones = parseFloat(this.totalRemuneracionesComputed);
        let totalExenciones = parseFloat(this.totalExencionesComputed);
        let totalDescuentos = parseFloat(this.totalDescuentosComputed);
        let totalNeto = totalRemuneraciones + totalExenciones - totalDescuentos;

        return totalNeto.toFixed(2);
      },
      set(total) {
        return total;
      },
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    dialogDetalleRecibo(val) {
      val || this.closeDetalleRecibo();
    },
    dialogDeleteRecibo(val) {
      val || this.closeDelete();
    },
    fecha() {
      this.dateFormatted = this.formatDate(this.fecha);
    },
  },

  created() {
    this.getRecibos();
    this.getEmpresas();
    this.getConceptos();
  },

  methods: {
    nuevoRecibo() {
      this.empresaSelected = null;
      this.empleadoSelected = {};
      this.periodoFecha = "";
      this.fecha = "";
      this.editedItem.recibo = 0;
      this.itemsRecibo = [];

      this.step++;
    },

    getRecibos() {
      db.collection("recibos")
        .get()
        .then((recibos) => {
          this.recibos = recibos;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    getRecibosFiltrados() {
      let empresa = this.empresaFiltroSelected;
      let empleado = this.empleadoFiltroSelected;
      let periodo = this.periodoFiltroFecha;

      db.collection("recibos")
        .get()
        .then((recibos) => {
          if (empresa != null) {
            this.getEmpleadosPorEmpresa(empresa.id);
          }

          this.recibos = recibos.filter((recibo) => {
            return (
              recibo.empresaSelected.id ==
                (empresa != null ? empresa.id : recibo.empresaSelected.id) &&
              recibo.empleadoSelected.id ==
                (empleado != null ? empleado.id : recibo.empleadoSelected.id) &&
              recibo.periodo == (periodo != null ? periodo : recibo.periodo)
            );
          });

          // this.recibos = recibos.filter((recibo) => {
          //   if (empleado == null) {
          //     if (periodo == null) {
          //       return recibo.empresaSelected.id == empresa.id;
          //     } else {
          //       return (
          //         recibo.empresaSelected.id == empresa.id &&
          //         recibo.periodo == periodo
          //       );
          //     }
          //   } else {
          //     if (periodo == null) {
          //       return (
          //         recibo.empresaSelected.id == empresa.id &&
          //         recibo.empleadoSelected.id == empleado.id
          //       );
          //     } else {
          //       return (
          //         recibo.empresaSelected.id == empresa.id &&
          //         recibo.empleadoSelected.id == empleado.id &&
          //         recibo.periodo == periodo
          //       );
          //     }
          //   }
          // });
          // } else {
          //   this.empleadoFiltroSelected = null;
          //   this.empleados = [];
          //   this.recibos = recibos;
          // }
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    getReciboDetalle() {
      db.collection("recibos")
        .get()
        .then((recibos) => {
          this.recibos = recibos;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    getEmpresas() {
      db.collection("empresas")
        .get()
        .then((empresas) => {
          this.empresas = empresas;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    empresaChange(empresa) {
      if (this.editedIndex == -1) {
        // Nuevo Recibo
        let siguienteRecibo = this.getSiguienteRecibo(empresa.id);
        this.editedItem.recibo = siguienteRecibo;
      }
      this.getEmpleadosPorEmpresa(empresa.id);
    },

    getEmpleadosPorEmpresa(id) {
      this.empleadoSelected = {};
      db.collection("empleados")
        .get()
        .then((empleados) => {
          this.empleados = empleados.filter((empleado) => {
            return empleado.idEmpresa == id;
          });
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    getSiguienteRecibo(idEmpresa) {
      let ultimoRecibo = 0;
      // Recibos por Empresa
      let recibos = this.recibos.filter((recibo) => {
        return recibo.idEmpresa == idEmpresa;
      });

      if (recibos.length > 0) {
        // Números de Recibo
        const numerosRecibo = recibos.map((item) => {
          return item.recibo;
        });
        // Ultimo N° de Recibo
        ultimoRecibo = Math.max(...numerosRecibo);
      }

      return ultimoRecibo + 1;
    },

    getConceptos() {
      db.collection("conceptos")
        .get()
        .then((conceptos) => {
          this.conceptos = conceptos;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    editItem(item) {
      this.editedIndex = this.recibos.findIndex((i) => i.id === item.id);
      this.getEmpresas();
      this.empresaSelected = item.empresaSelected;

      this.empleados = [];
      this.empleados.push(item.empleadoSelected);
      this.empleadoSelected = item.empleadoSelected;

      this.periodoFecha = item.periodo;
      this.fecha = item.fecha;
      // Detalle
      this.itemsRecibo = item.detalle;
      this.editedItem = Object.assign({}, item);

      this.step++;
    },

    duplicateItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialogDuplicate = true;
    },

    duplicateItemConfirm() {
      this.editedItem.id = new Date().getTime(); // Nuevo Id
      // this.recibos.push(Object.assign({}, this.editedItem));

      db.collection("recibos")
        .add(this.editedItem)
        .then(() => {
          // this.close();
          this.getRecibos();
        })
        .catch((error) => {
          console.log("Error " + error);
        });

      this.dialogDuplicate = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
      });
    },

    deleteItem(item) {
      this.editedIndex = this.recibos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    printItem(item) {
      this.itemsRecibo = item.detalle;
      this.editedItem = Object.assign({}, item);
      this.step = 3;
    },

    deleteItemConfirm() {
      this.recibos.splice(this.editedIndex, 1);

      db.collection("recibos")
        .doc({ id: this.editedItem.id })
        .delete()
        .then((response) => {
          console.log("Eliminado: " + JSON.stringify(response.data.keys));
        })
        .catch((error) => {
          console.log("Error " + error);
        });

      this.closeDelete();
    },

    close() {
      this.getRecibos();
      this.step--;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      var isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }

      if (this.editedIndex > -1) {
        // Edit
        this.editedItem.fecha = this.fecha;

        db.collection("recibos")
          .doc({ id: this.editedItem.id })
          .update({
            idEmpresa: this.editedItem.idEmpresa,
            empresa: this.editedItem.empresa,
            empresaSelected: this.empresaSelected,
            idEmpleado: this.editedItem.idEmpleado,
            empleado: `${this.empleadoSelected.apellido}, ${this.empleadoSelected.nombre}`,
            empleadoSelected: this.empleadoSelected,
            periodo: this.periodoFecha,
            fecha: this.editedItem.fecha,
            recibo: this.editedItem.recibo,
            detalle: this.itemsRecibo,
          })
          .then(() => {
            this.close();
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      } else {
        // Add
        this.editedItem.id = new Date().getTime();
        this.editedItem.idEmpresa = this.empresaSelected.id;
        this.editedItem.empresa = this.empresaSelected.nombre;
        this.editedItem.empresaSelected = this.empresaSelected;
        this.editedItem.idEmpleado = this.empleadoSelected.id;
        this.editedItem.empleado = `${this.empleadoSelected.apellido}, ${this.empleadoSelected.nombre}`;
        this.editedItem.empleadoSelected = this.empleadoSelected;
        this.editedItem.periodo = this.periodoFecha;
        this.editedItem.fecha = this.fecha;
        this.editedItem.detalle = this.itemsRecibo;

        this.recibos.push(this.editedItem);

        db.collection("recibos")
          .add(this.editedItem)
          .then(() => {
            this.close();
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      }
    },

    print() {
      window.print();
    },

    closePrint() {
      this.getRecibos();
      this.step = 1;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    // ---- Items del Recibo ----
    editItemDetalleRecibo(item) {
      this.editedIndexRecibo = this.itemsRecibo.indexOf(item);
      this.conceptoSelected = item.conceptoSelected;
      this.editedItemRecibo = Object.assign({}, item);
      this.dialogDetalleRecibo = true;
    },

    deleteItemDetalleRecibo(item) {
      this.editedIndexRecibo = this.itemsRecibo.indexOf(item);
      this.editedItemRecibo = Object.assign({}, item);
      this.dialogDetalleReciboDelete = true;
    },

    deleteItemConfirmDetalleRecibo() {
      this.itemsRecibo.splice(this.editedIndexRecibo, 1);
      this.closeDeleteDetalleRecibo();
    },

    closeDetalleRecibo() {
      this.dialogDetalleRecibo = false;
      this.$nextTick(() => {
        this.editedItemRecibo = Object.assign({}, this.defaultItemRecibo);
        this.editedIndexRecibo = -1;
      });
    },

    closeDeleteDetalleRecibo() {
      this.dialogDetalleReciboDelete = false;
      this.$nextTick(() => {
        this.editedItemRecibo = Object.assign({}, this.defaultItemRecibo);
        this.editedIndexRecibo = -1;
      });
    },

    saveDetalleRecibo() {
      var isValid = this.$refs.formDetalleRecibo.validate();
      if (!isValid) {
        return;
      }

      if (this.editedIndexRecibo > -1) {
        // Edit
        this.setDataItemRecibo();
        Object.assign(
          this.itemsRecibo[this.editedIndexRecibo],
          this.editedItemRecibo
        );
      } else {
        // Add
        this.editedItemRecibo.id = new Date().getTime();
        this.setDataItemRecibo();

        this.itemsRecibo.push(this.editedItemRecibo);

        // Sumar items del recibo
        //this.editedItem.detalle = Object.assign({}, this.itemsRecibo);
      }
      this.closeDetalleRecibo();
    },

    setDataItemRecibo() {
      this.editedItemRecibo.codigo = this.conceptoSelected.codigo;
      this.editedItemRecibo.concepto = this.conceptoSelected.nombre;
      this.editedItemRecibo.conceptoSelected = this.conceptoSelected;

      this.editedItemRecibo.retenciones = "";
      this.editedItemRecibo.exenciones = "";
      this.editedItemRecibo.descuentos = "";

      let total = parseFloat(this.editedItemRecibo.total).toFixed(2);

      switch (this.conceptoSelected.tipo) {
        case "retencion":
          this.editedItemRecibo.retenciones = total;

          break;
        case "exento":
          this.editedItemRecibo.exenciones = total;

          break;
        case "descuento":
          this.editedItemRecibo.descuentos = total;
          break;
        default:
          break;
      }
    },

    // Fin Detalle Recibo

    totalConcepto() {
      let unitario = parseFloat(this.editedItemRecibo.unitario);
      let unidaddes = parseInt(this.editedItemRecibo.unidades);
      let alicuota = parseFloat(this.editedItemRecibo.alicuota);

      this.editedItemRecibo.total = 0;

      if (!isNaN(unitario) && !isNaN(unidaddes)) {
        let total = parseFloat(unidaddes * unitario).toFixed(2);
        if (!isNaN(alicuota)) {
          total = (total * alicuota) / 100;
        }
        this.editedItemRecibo.total = parseFloat(total).toFixed(2);
      }
    },

    nuevo() {
      this.conceptoSelected = null;
    },

    conceptoChange(concepto) {
      this.editedItemRecibo.alicuota = "";
      if (!isNaN(concepto.alicuota) && concepto.alicuota.trim() != "") {
        this.editedItemRecibo.alicuota = parseFloat(concepto.alicuota).toFixed(
          2
        );
      }
      this.totalConcepto();
    },

    fechaRecibo(fecha) {
      return this.formatDate(fecha);
    },

    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },

    ISOToDate(date) {
      if (!date) return null;

      let dateISO = date.substring(0, 10);
      const [year, month, day] = dateISO.split("-");
      return `${day}/${month}/${year}`;
    },

    aceptarFiltroPeriodo() {
      this.$refs.dialogPeriodoFiltro.save(this.periodoFiltroFecha);
      this.getRecibosFiltrados();
    },

    limparFiltroPeriodo() {
      this.periodoFiltroFecha = null;
      this.getRecibosFiltrados();
    },
  },
};
</script>

