  .no-printme {
    display: none;
  }
  .printme {
    display: block;
  }
}

.v-card__subtitle {
  font-size: 8pt;
}
.v-card__text {
  font-size: 10pt;
}
.v-card {
  min-height: 28px;
}

.itemRecibo {
  border-bottom-color: white;
  border-top-color: white;
}
