export const reglasMixin = {

    data: () => ({
        reglas: {
            required: (value) => !!value || "Completar.",
            validDate: (v) =>
                (v != null &&
                /^([0]?[1-9]|[1|2][0-9]|[3][0|1])[/]([0]?[1-9]|[1][0-2])[/]([0-9]{4}|[0-9]{2})$/.test(
                    v
                )) ||
                "Fecha inválida",
            email: (value) => {
                const pattern =
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                var validMail = pattern.test(value);
                if (value != "" && validMail) {
                return true || console.log("Valor: " + value);
                }
                return false || "Correo inválido.";
            },
            correo: (value) => {
                if (value) {
                const pattern =
                    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                var validMail = pattern.test(value);
                if (!validMail) {
                    return "Correo inválido.";
                }
                return true;
                }
                return true;
            },
            soloNumeros: (value) => {
                if (isNaN(value)) return "Sólo números";
                return true;
            },
            soloNumerosPositivos: (value) => {
                if (isNaN(value)) return "Sólo números";
                if (value < 0) return "Sólo números positivos";
                return true;
            },
            soloNumerosMayoresCero: (value) => {
                if (isNaN(value)) return "Sólo números";
                if (value <= 0) return "Sólo números positivos";
                return true;
            },
            maximoCaracteres(max) {
                return (v) =>
                (v || "").length <= max || "Máximo " + max + " caracteres";
            },
            minimoCaracteres(min) {
                return (v) =>
                (v || "").length >= min || "Mínimo " + min + " caracteres";
            },            
            maximoNumeros(max) {
                return (v) => v.length <= max || "Máximo " + max + " números";
            },
            numerosDesde(min) {
                return (v) => v >= min || "Mínimo " + min;
            },
            numerosHasta(max) {
                return (v) => v <= max || "Máximo " + max;
            },
        },
    }),

  }