<template>
  <v-data-table
    :headers="headers"
    :items="conceptos"
    sort-by="codigo"
    class="elevation-1"
    :items-per-page="5"
    locale="es-AR"
    no-results-text="No hay resultados"
    no-data-text="No hay datos"
    :footer-props="{
      showFirstLastPage: true,
      'items-per-page-text': 'Registros',
      'items-per-page-options': [5, 10, 25, 50],
    }"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Conceptos</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Nuevo Concepto
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-form ref="form" lazy-validation>
                <v-row>
                  <!-- Código -->
                  <v-col cols="12" md="4">
                    <v-text-field
                      v-model="editedItem.codigo"
                      label="Código"
                      :rules="[reglas.required, reglas.soloNumeros]"
                    ></v-text-field>
                  </v-col>
                  <!-- Nombre-->
                  <v-col cols="12" md="8">
                    <v-text-field
                      v-model="editedItem.nombre"
                      label="Nombre"
                      :rules="[reglas.required]"
                    ></v-text-field>
                  </v-col>
                </v-row>

                <v-row class="mt-0">
                  <!-- Tipo -->
                  <v-col cols="12" class="pt-0 pb-0">
                    <v-radio-group
                      v-model="editedItem.tipo"
                      col
                      class="mt-0"
                      :rules="[reglas.required]"
                    >
                      <template v-slot:label>
                        <div>Tipo de concepto</div>
                      </template>
                      <v-radio
                        label="Remuneración sujeta a retención"
                        value="retencion"
                      ></v-radio>
                      <v-radio
                        label="Remuneración exenta"
                        value="exento"
                      ></v-radio>
                      <v-radio label="Descuento" value="descuento"></v-radio>
                    </v-radio-group>
                  </v-col>
                </v-row>

                <v-row>
                  <!-- Código -->
                  <v-col cols="12" md="4">
                    <v-text-field
                      v-model="editedItem.alicuota"
                      label="Alícuota"
                      :rules="[
                        reglas.soloNumerosPositivos,
                        reglas.maximoNumeros(4),
                        reglas.numerosHasta(100),
                      ]"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancelar
              </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Aceptar </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">¿Elimina el Concepto?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancelar</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >Aceptar</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:[`item.actions`]="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import { reglasMixin } from "../mixins/reglasMixin";
import Localbase from "localbase";

let db = new Localbase("db");
db.config.debug = false;

export default {
  name: "ConceptosView",
  mixins: [reglasMixin],
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "Código", value: "codigo", sortable: true },
      {
        text: "Nombre",
        align: "start",
        sortable: true,
        value: "nombre",
      },
      { text: "", value: "actions", sortable: false },
    ],
    conceptos: [],
    editedIndex: -1,
    editedItem: {
      id: "",
      codigo: "",
      nombre: "",
      tipo: "retencion",
      alicuota: "",
    },
    defaultItem: {
      id: "",
      codigo: "",
      nombre: "",
      tipo: "retencion",
      alicuota: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo Concepto" : "Editar Concepto";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.getConceptos();
  },

  methods: {
    getConceptos() {
      db.collection("conceptos")
        .get()
        .then((conceptos) => {
          this.conceptos = conceptos;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    editItem(item) {
      this.editedIndex = this.conceptos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.conceptos.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.conceptos.splice(this.editedIndex, 1);

      db.collection("conceptos")
        .doc({ id: this.editedItem.id })
        .delete()
        .then((response) => {
          console.log("Eliminado: " + JSON.stringify(response.data.keys));
        })
        .catch((error) => {
          console.log("Error " + error);
        });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      var isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }

      if (this.editedIndex > -1) {
        // Edit
        Object.assign(this.conceptos[this.editedIndex], this.editedItem);

        db.collection("conceptos")
          .doc({ id: this.editedItem.id })
          .update({
            codigo: this.editedItem.codigo,
            nombre: this.editedItem.nombre,
            tipo: this.editedItem.tipo,
            alicuota: this.editedItem.alicuota,
          })
          .then((response) => {
            console.log("Actualizado: " + JSON.stringify(response.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      } else {
        // Add
        this.editedItem.id = new Date().getTime();
        this.conceptos.push(this.editedItem);

        db.collection("conceptos")
          .add(this.editedItem)
          .then((response) => {
            console.log("Nuevo: " + JSON.stringify(response.data.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      }
      this.close();
    },
  },
};
</script>
