<template>
  <v-data-table
    :headers="headers"
    :items="empresas"
    sort-by="nombre"
    class="elevation-1"
    :items-per-page="5"
    locale="es-AR"
    no-results-text="No hay resultados"
    no-data-text="No hay datos" 
    :footer-props="{
      showFirstLastPage: true,
      'items-per-page-text': 'Registros',
      'items-per-page-options': [5, 10, 25, 50],
    }"     
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Empresas</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
              Nueva Empresa
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-form ref="form" lazy-validation>
                  <v-row>
                    <v-col cols="12" md="12">
                      <v-text-field
                        v-model="editedItem.nombre"
                        label="Razón Social"
                        :rules="[reglas.required]"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" md="12">
                      <v-text-field
                        v-model="editedItem.domicilio"
                        label="Domicilio"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="editedItem.cuit"
                        label="C.U.I.T."
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="editedItem.cuil"
                        label="C.U.I.L."
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancelar
              </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Aceptar </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">¿Elimina la Empresa?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancelar</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >Aceptar</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:[`item.actions`]="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import { reglasMixin } from "../mixins/reglasMixin";
import Localbase from "localbase";

let db = new Localbase("db");
db.config.debug = false;

export default {
  name: "EmpresasView",
  mixins: [reglasMixin],
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Razón Social",
        align: "start",
        sortable: true,
        value: "nombre",
      },
      { text: "Domicilio", value: "domicilio", sortable: false },
      { text: "C.U.I.T.", value: "cuit", sortable: false },
      { text: "C.U.I.L.", value: "cuil", sortable: false },
      { text: "", value: "actions", sortable: false },
    ],
    empresas: [],
    editedIndex: -1,
    editedItem: {
      id: 0,
      nombre: "",
      domicilio: "",
      cuit: "",
      cuil: "",
    },
    defaultItem: {
      id: 0,
      nombre: "",
      domicilio: "",
      cuit: "",
      cuil: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nueva Empresa" : "Editar Empresa";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.getEmpresas();
  },

  methods: {
    getEmpresas() {
      db.collection("empresas")
        .get()
        .then((empresas) => {
          this.empresas = empresas;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    editItem(item) {
      this.editedIndex = this.empresas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.empresas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.empresas.splice(this.editedIndex, 1);

      db.collection("empresas")
        .doc({ id: this.editedItem.id })
        .delete()
        .then((response) => {
          console.log("Eliminado: " + JSON.stringify(response.data.keys));
        })
        .catch((error) => {
          console.log("Error " + error);
        });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      var isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }

      if (this.editedIndex > -1) {
        Object.assign(this.empresas[this.editedIndex], this.editedItem);

        db.collection("empresas")
          .doc({ id: this.editedItem.id })
          .update({
            nombre: this.editedItem.nombre,
            domicilio: this.editedItem.domicilio,
            cuit: this.editedItem.cuit,
            cuil: this.editedItem.cuil,
          })
          .then((response) => {
            console.log("Actualizado: " + JSON.stringify(response.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      } else {
        this.editedItem.id = new Date().getTime();
        this.empresas.push(this.editedItem);

        db.collection("empresas")
          .add(this.editedItem)
          .then((response) => {
            console.log("Nuevo: " + JSON.stringify(response.data.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      }
      this.close();
    },
  },
};
</script>
