<template>
  <v-data-table
    :headers="headers"
    :items="empleados"
    sort-by="nombre"
    class="elevation-1"
    :items-per-page="5"
    locale="es-AR"
    no-results-text="No hay resultados"
    no-data-text="No hay datos" 
    :footer-props="{
      showFirstLastPage: true,
      'items-per-page-text': 'Registros',
      'items-per-page-options': [5, 10, 25, 50],
    }"     
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Empleados</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="600px">
          <template v-slot:activator="{ on, attrs }">
            <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on" @click="nuevo">
              Nuevo Empleado
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-form ref="form" lazy-validation>
                  <v-row>
                    <!-- Empresa -->
                    <v-col cols="12">
                      <v-select
                        v-model="empresaSelected"
                        :items="empresas"
                        item-text="nombre"
                        item-value="id"
                        label="Empresa"
                        return-object
                        single-line
                        no-data-text="Sin datos"
                        :rules="[reglas.required]"
                      ></v-select>
                    </v-col>
                  </v-row>

                  <v-row>
                    <!-- Nombre -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.nombre"
                        label="Nombre"
                        :rules="[reglas.required]"
                      ></v-text-field>
                    </v-col>
                    <!-- Apellido -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.apellido"
                        label="Apellido"
                        :rules="[reglas.required]"
                      ></v-text-field>
                    </v-col>
                    <!-- Documento -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.documento"
                        label="Documento"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row>
                    <!-- Jerarquía -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.jerarquia"
                        label="Jerarquia"
                      ></v-text-field>
                    </v-col>
                    <!-- Categoria -->
                    <v-col cols="12" md="8" lg="8">
                      <v-text-field
                        v-model="editedItem.categoria"
                        label="Categoria"
                      ></v-text-field>
                    </v-col>
                  </v-row>

                  <v-row>
                    <!-- Legajo -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.legajo"
                        label="Legajo"
                      ></v-text-field>
                    </v-col>
                    <!-- Ingreso -->
                    <v-col cols="12" md="4" lg="4">
                      <v-menu
                        v-model="menuIngreso"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedIngreso"
                            label="Ingreso"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                            clearable
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="ingreso"
                          no-title
                          @input="menuIngreso = false"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>

                    <!-- Egreso -->
                    <v-col cols="12" md="4" lg="4">
                      <v-menu
                        v-model="menuEgreso"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedEgreso"
                            label="Egreso"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                            clearable
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="egreso"
                          no-title
                          @input="menuEgreso = false"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>

                  <v-row>
                    <!-- Salario Básico -->
                    <v-col cols="12" md="4" lg="4">
                      <v-text-field
                        v-model="editedItem.salarioBasico"
                        label="Salario Básico"
                        :rules="[reglas.required, reglas.soloNumeros]"
                      ></v-text-field>
                    </v-col>
                    <!-- Obra Social -->
                    <v-col cols="12" md="8" lg="8">
                      <v-text-field
                        v-model="editedItem.obraSocial"
                        label="Obra Social"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-form>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">
                Cancelar
              </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Aceptar </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">¿Elimina al Empleado?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete"
                >Cancelar</v-btn
              >
              <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                >Aceptar</v-btn
              >
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>

    <template v-slot:[`item.actions`]="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
      <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import { reglasMixin } from "../mixins/reglasMixin";
import Localbase from "localbase";

let db = new Localbase("db");
db.config.debug = false;

export default {
  name: "EmpleadosView",
  mixins: [reglasMixin],
  data: (vm) => ({
    ingreso: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substring(0, 10),
    egreso: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
      .toISOString()
      .substring(0, 10),
    dateFormatted: vm.formatDate(
      new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substring(0, 10)
    ),
    menuIngreso: false,
    menuEgreso: false,
    dialog: false,
    dialogDelete: false,
    headers: [
      { text: "Apellido", value: "apellido", sortable: true },
      {
        text: "Nombre",
        align: "start",
        sortable: false,
        value: "nombre",
      },

      { text: "Documento", value: "documento", sortable: false },
      { text: "Jerarquia", value: "jerarquia", sortable: false },
      { text: "Categoria", value: "categoria", sortable: false },
      { text: "Legajo", value: "legajo", sortable: false },
      { text: "Empresa", value: "empresa.nombre", sortable: true },
      { text: "", value: "actions", sortable: false },
    ],
    empleados: [],
    editedIndex: -1,
    editedItem: {
      id: 0,
      apellido: "",
      nombre: "",
      documento: "",
      jerarquia: "",
      categoria: "",
      legajo: "",
      ingreso: "",
      obraSocial: "",
      salarioBasico: "",
      egreso: "",
      idEmpresa: 0,
      empresa:{},
    },
    defaultItem: {
      id: 0,
      apellido: "",
      nombre: "",
      documento: "",
      jerarquia: "",
      categoria: "",
      legajo: "",
      ingreso: "",
      obraSocial: "",
      salarioBasico: "",
      egreso: "",
      idEmpresa: 0,
      empresa:{},
    },

    empresaSelected: null,
    empresas: [],
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nuevo Empleado" : "Editar Empleado";
    },
    computedIngreso: {
      get() {
        return this.formatDate(this.ingreso);
      },
      set(ingreso) {
        return ingreso;
      },
    },
    computedEgreso: {
      get() {
        return this.formatDate(this.egreso);
      },
      set(egreso) {
        return egreso;
      },
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    ingreso() {
      this.dateFormatted = this.formatDate(this.ingreso);
    },
    egreso() {
      this.dateFormatted = this.formatDate(this.egreso);
    },
  },

  created() {
    this.getEmpleados();
    this.getEmpresas();
  },

  methods: {
    getEmpleados() {
      db.collection("empleados")
        .get()
        .then((empleados) => {
          this.empleados = empleados;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    getEmpresas() {
      db.collection("empresas")
        .get()
        .then((empresas) => {
          this.empresas = empresas;
        })
        .catch((error) => {
          console.log("Error " + error);
        });
    },

    nuevo(){
      this.empresaSelected = null;
      this.ingreso = "";
      this.egreso = "";      
    },

    editItem(item) {
      this.editedIndex = this.empleados.indexOf(item);
      this.empresaSelected = item.empresa;
      this.ingreso = item.ingreso;
      this.egreso = item.egreso;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.empleados.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.empleados.splice(this.editedIndex, 1);

      db.collection("empleados")
        .doc({ id: this.editedItem.id })
        .delete()
        .then((response) => {
          console.log("Eliminado: " + JSON.stringify(response.data.keys));
        })
        .catch((error) => {
          console.log("Error " + error);
        });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      var isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }

      if (this.editedIndex > -1) { // Edit
        this.editedItem.ingreso = this.ingreso;
        this.editedItem.egreso = this.egreso;

        Object.assign(this.empleados[this.editedIndex], this.editedItem);

        db.collection("empleados")
          .doc({ id: this.editedItem.id })
          .update({
            apellido: this.editedItem.apellido,
            nombre: this.editedItem.nombre,
            documento: this.editedItem.documento,
            jerarquia: this.editedItem.jerarquia,
            categoria: this.editedItem.categoria,
            legajo: this.editedItem.legajo,
            ingreso: this.editedItem.ingreso,
            obraSocial: this.editedItem.obraSocial,
            salarioBasico: this.editedItem.salarioBasico,
            egreso: this.editedItem.egreso,
            idEmpresa: this.empresaSelected.id,
            empresa: this.empresaSelected,
          })
          .then((response) => {
            console.log("Actualizado: " + JSON.stringify(response.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      } else { // Add
        this.editedItem.id = new Date().getTime(); // Genero un Id
        this.editedItem.idEmpresa = this.empresaSelected.id;
        this.editedItem.empresa = this.empresaSelected;
        this.empleados.push(this.editedItem);

        db.collection("empleados")
          .add(this.editedItem)
          .then((response) => {
            console.log("Nuevo: " + JSON.stringify(response.data.data));
          })
          .catch((error) => {
            console.log("Error " + error);
          });
      }
      this.close();
    },

    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },

    // parseDate(date) {
    //   if (!date) return null;

    //   const [month, day, year] = date.split("/");
    //   return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    // },
  },
};
</script>
